@import "reset.scss";


@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap');


body {
    font-family: 'Poppins'; }


.container {
    max-width: 1170px;
    margin: 0 auto; }

.header {
    padding-top: 46px;
    min-height: 770px; }


.nav {
    display: flex;
    justify-content: space-between;
    height: 32px;
    &__list {
        display: flex;
        column-gap: 50px;
        align-items: flex-end;
        &-item-link {
            font-weight: 500;
            font-size: 14px;
            line-height: 21px;
            /* identical to box height */


            color: #000000; } } }


.logo {
    position: relative;
    font-weight: 700;
    font-size: 32px;
    line-height: 100%;
    color: #000000;
    &::after {
        content: '';
        position: absolute;
        width: 6px;
        height: 6px;
        bottom: 0px;
        right: -8px;
        border-radius: 50%;
        background-color: #FDA300; } }


